import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { environment as env } from '../../environments/environment';
import { Config } from '../services/config';

@Injectable({
  providedIn: 'root'
})
export class AdminService {
  config = new Config;
  constructor(private http: Http ) {}
  getAdminInfo() {
      let currentUser = JSON.parse(localStorage.getItem('currentUser'));
      console.log(currentUser.data._id)
      let headers: Headers = new Headers();
      headers.append("sessionToken", currentUser.sessionToken); 
      headers.append("Content-Type", "application/json");
      const uri = env.APIURL + '/admin/admin-info/'+currentUser.data._id;
      return this
          .http
          .get(uri, {headers: headers})
          .map((res: Response) => {
              return res.json();
          })
          .catch((error: Response) => {
              error = error.json();
              return Observable.throw(error);
          });
  }
  getAdminActivity() {
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
    console.log(currentUser.data._id)
    let headers: Headers = new Headers();
    headers.append("sessionToken", currentUser.sessionToken); 
    headers.append("Content-Type", "application/json");
    const uri = env.APIURL + '/admin/my-activity';
    return this
        .http
        .get(uri, {headers: headers})
        .map((res: Response) => {
            return res.json();
        })
        .catch((error: Response) => {
            error = error.json();
            return Observable.throw(error);
        });
  }
  getActivitynext(a) {
    
    let con = a+1;
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
   // console.log(currentUser)
    let headers: Headers = new Headers();
    headers.append("sessionToken", currentUser.sessionToken); 
    headers.append("Content-Type", "application/json");
    const uri = env.APIURL + '/admin/my-activity?page='+con;
    return this
        .http
        .get(uri, {headers: headers})
        .map((res: Response) => {
            return res.json();
        })
        .catch((error: Response) => {
            error = error.json();
            return Observable.throw(error);
        });
  }
  getActivitypre(a) {

    let con = a-2;
    //alert(con)
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
   // console.log(currentUser)
    let headers: Headers = new Headers();
    headers.append("sessionToken", currentUser.sessionToken); 
    headers.append("Content-Type", "application/json");
    const uri = env.APIURL + '/admin/my-activity?page='+con;
    return this
        .http
        .get(uri, {headers: headers})
        .map((res: Response) => {
            return res.json();
        })
        .catch((error: Response) => {
            error = error.json();
            return Observable.throw(error);
        });
  }
  updatePassword(data) {
    let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        // console.log(currentUser)
    let headers: Headers = new Headers();
    headers.append("sessionToken", currentUser.sessionToken); 
    headers.append("Content-Type", "application/json");
    const uri = env.APIURL + '/admin/change-password';
    return this
        .http
        .post(uri, data,  {headers: headers})
        .map((res: Response) => {
            return res.json()
        })
        .catch((error: Response) => {
            error = error.json();
            return Observable.throw(error);
        });
  }
}
